.banner {
  background: linear-gradient(to right, rgb(40, 119, 250), rgb(103, 23, 205));
  position: relative;
  background: url(../../../public/images/laptop-gbbedcf24d_1920.jpeg) no-repeat
    center;
  background-size: cover;
  width: 100%;
  height: 600px;
  overflow: hidden;
  .container {
    color: #fff;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
    font-size: 1.6rem;
    justify-content: center;
    line-height: 1.6;
    max-width: 100%;
    position: absolute;
    width: 640px;
    left: 120px;
    top: 50%;
    h2 {
      margin-bottom: 8px;
      font-size: 32px;
    }
    .content {
      margin-bottom: 24px;
      margin-top: 0;
      max-width: 600px;
      font-size: 18px;
    }
  }
}
